import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { IonTabs } from '@ionic/angular';
import { SpotifyAuthService } from 'src/app/core/services/spotify-auth.service';

@Component({
  selector: 'app-music-tab',
  templateUrl: './music-tab.page.html',
  styleUrls: ['music-tab.page.scss'],
})
export class MusicTabPage implements OnInit {

  items: SpotifyApi.PlaylistObjectSimplified[] = [];
  
  responsePlayList: SpotifyApi.PagingObject<SpotifyApi.PlaylistObjectSimplified>;
  title: string = 'Music';

  constructor(private spotifyAuthService: SpotifyAuthService) {}
  ngOnInit(): void {
    this.loadList();
  }
  async loadList() {
    this.responsePlayList = await this.spotifyAuthService.getPlayList();
    this.items = this.responsePlayList.items;
    console.log(this.responsePlayList.items);
  }

}